            :zoom="mapZoom" 
            @zoomend="handleZoomChange" >
            <template v-if="polylinePath.length >= 2">
              <bm-marker :position="polylinePath[0]" :icon="startMark"></bm-marker>
              <bm-marker :position="polylinePath[polylinePath.length - 1]" :icon="endMark"></bm-marker>
            </template>
            <bm-polyline :path="polylinePath" stroke-color="blue" :stroke-opacity="0.5" :stroke-weight="5"></bm-polyline>
          </baidu-map>
        </div>
      </div>
    </transition>
    
    <transition :name="animationName">
      <div class="page page-two" v-show="showPage === 1">
        <div class="page-block">
          <div class="consume-wrap">
