              <div class="text">仅消耗能量<span class="key-word">24</span>千瓦时</div>
              <div class="text">居然还可以省下￥<span class="key-word">10</span>元</div>
            </div>
          </div>
        </div>

        <div class="page-block">
          <div class="save-money-wrap">
            <div class="flex-left">
              <div class="text">一个月玩耍<span class="key-word">2323</span>公里</div>
              <div class="text">一年预计可<span class="key-word">省下</span></div>
              <div class="money">￥ 15162</div>
            </div>

            <div class="flex-right">
              <img src="../../assets/image/money.png">
            </div>
          </div>
          <div class="row large-font">可以用来创造更多浪漫美好的小惊喜</div>
        </div>

        <div class="page-block">
          <div class="compare-wrap">
            <div class="compare-item">
              <div class="flex-left">
                <img src="../../assets/image/lightning.png">
              </div>
              <div class="flex-right">
                <div><span class="money">236</span><span class="unit">元</span></div>
                <div>电耗 0.5 元/公里</div>
              </div>
            </div>
            
            <div class="vs-wrap">
              <div class="vs"><img class="image" src="../../assets/image/vs.png"></div>
            </div>
            
            <div class="compare-item">
              <div class="flex-left">
                <img src="../../assets/image/water-drop.png">
              </div>
              <div class="flex-right">
                <div><span class="money">236</span><span class="unit">元</span></div>
                <div>油耗 1.2 元/公里</div>
              </div>
            </div>
          </div>
        </div>

        <div class="page-block">
          <img class="image" src="../../assets/image/car.png">
        </div>
      </div>
    </transition>

    <transition :name="animationName">
      <div class="page page-three" v-show="showPage === 2">
        <div class="page-block">
          <div class="nezha-title">此款风火轮还自带高科技哦</div>
        </div>

        <div class="page-block">
          <div class="operation-wrap">
            <div class="desc">使用<span class="key-word">ACC自适应巡航</span>2次</div>
            <div class="image-wrap"><img src="../../assets/image/car.png" alt=""></div>
          </div>
        </div>

        <div class="page-block">
          <div class="operation-wrap">
            <div class="desc">使用<span class="key-word">车道保持辅助</span>2次</div>
            <div class="image-wrap"><img src="../../assets/image/car.png" alt=""></div>
          </div>
        </div>

        <div class="page-block">
          <div class="operation-wrap">
            <div class="desc">使用<span class="key-word">一键自动泊车</span>2次</div>
            <div class="image-wrap"><img src="../../assets/image/car.png" alt=""></div>
          </div>
        </div>

      </div>
    </transition>

    <transition :name="animationName">
      <div class="page page-four" v-show="showPage === 3">
        <div class="page-block">
          <div class="title">还有更多的技能等待你发现</div>
        </div>
        <div class="page-block">
          <transition name="card">
            <div class="rotate-card-wrap">
              <div class="card"
                v-for="(item, index) in notIntroduceList"
                :key="item.title"
                :class="'card-' + (item.seq + 1)"
                @click="handleTapCard(index)">
                <div class="card-title">{{ item.title }}</div>
                <img class="card-image" :src="item.img">
              </div>
            </div>
          </transition>
        </div>
        <div class="page-block">
          <van-button type="info" round block @click="goValuation">快点我！盘它！盘它！</van-button>
        </div>
      </div>
    </transition>
  </v-touch>
</template>

<script>
import startpoint from '@/assets/image/startpoint.png'
import endpoint from '@/assets/image/endpoint.png'

import m1 from '@/assets/image/1.jpg'
import m2 from '@/assets/image/2.jpg'
import m3 from '@/assets/image/3.jpg'
import m4 from '@/assets/image/4.jpg'
import m5 from '@/assets/image/5.jpg'
import m6 from '@/assets/image/6.jpg'

export default {
  data() {
    return {
      showPage: 1, // 当前显示的页面
      swipeDirection: 'down', // 滑动方向，上滑 up, 下滑 down
      detail: {},
      mapZoom: 15,
      centerPoint: { lng: 0, lat: 0 },
      startMark:{
        url: startpoint,
        size: {width: 32, height:32},
        opts: {anchor: {width: 16, height:26}}
      },
      endMark:{
        url: endpoint,
        size: {width: 32, height: 32},
        opts: {anchor: {width: 16, height:26}}
      },
      polylinePath: [{lng: 121.53353772041, lat: 29.871632207843},
  {lng: 121.53352868808, lat: 29.87174102118},
  {lng: 121.53358725051, lat: 29.871515466014}],

      notIntroduceList: [{
        title: '感应尾门',
        img: m1,
      },{
        title: '马牌轮胎',
        img: m2,
      },{
        title: '豪华内饰',
        img: m3,
      },
      {
        title: '360全景影像',
        img: m4
      },{
        title: '超大后排空间',
        img: m5
      },{
        title: '智能座椅',
        img: m6
      }
      ]
    }
  },
  computed: {
    // 动画名称
    animationName() {
      return 'page-' + this.swipeDirection
    }
  },
  created() {
    this.getReportInfo()

    this.notIntroduceList.forEach((item, index) => {
      this.$set(item, 'seq', index)
    })
  },
  methods: {

    getReportInfo() {
      // this.handleMapReady()
    },
    /**
     * 手指往上滑，进入下一页
     */
    swipeup() {
      if (this.showPage < 3) {
        this.swipeDirection = 'down'
        this.showPage = this.showPage + 1
      }
    },
    /**
     * 手指往下滑，进入上一页
     */
    swipedown() {
      if (this.showPage >= 1) {
        this.swipeDirection = 'up'
        this.showPage = this.showPage - 1 
      }
    },
    // 地图加载完成
    handleMapReady(e) {
      if(e && !this.map) {
        this.map = e.map
      }
      if(this.polylinePath.length > 0) {
        let view = this.map.getViewport(eval(this.polylinePath));
        this.mapZoom = view.zoom;
        this.centerPoint = view.center;
      }
    },
    // 地图缩放结束
    handleZoomChange(e) {
      this.mapZoom = e.target.getZoom()
    },
    // 去 评价
    goValuation() {
      this.$router.push('/customerValuation')
    },
    // handleTapCard
    handleTapCard(index) {
      // console.log(index)
      //let current = this.notIntroduceList[index]
      // this.notIntroduceList.splice(0, 1)
      // this.notIntroduceList.unshift(current)
       
      //  this.notIntroduceList.map() 

      this.notIntroduceList.forEach(item => {
        if (item.seq === this.notIntroduceList.length - 1) {
          item.seq = 0
        } else {
          item.seq = item.seq + 1
        }
      })

    }
  }
}
</script>

<style scoped lang="less">
.report-wrap {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  font-size: 14px;
  .page {
    width: 100%;
    height: 100%;
    padding: 10px 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #000000d9;
    .page-block {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
  }

  .large-font {
    font-size: 15px;
  }

  .row {
    line-height: 28px;
    letter-spacing: 0.5px;
  }

  .key-word {
    font-weight: 600;
    font-size: 16px;
    letter-spacing: 1px;
    padding: 0 4px;
    &.first {
      padding-left: 0;
    }
  }

  .image {
    width: 100%;
    max-height: 100%;
    border-radius: 12px;
  }

  .landscape-wrap {
    position: relative;
    height: 80px;
    display: flex;

    .text {
      white-space: nowrap;

      &.text-left {
        position: relative;
        top: 6px;
      }

      &.text-right {
        align-self: flex-end;
        position: relative;
        bottom: 10px;
      }
    }

    .landscape-image-wrap {
      height: 100%;
      flex-grow: 1;

      .landscape-image {
        width: 100%;
        height: 100%;
        object-fit: fill;
        position: relative;
        left: -4px;
        @media (min-width: 500px) {
          object-fit: contain;
        }
      }
    }
  }

  .map {
    width: 100%;
    height: 100%;
    border: 1px solid #e3e0ed;
    box-shadow: inset 0 0 1px 1px #ccc;
  }

  @media (max-width: 300px) {
    font-size: 13px;
    .key-word {
      font-size: 14px;
    }
    .large-font {
      font-size: 14px;
    }
  }  

  // 按媒体设置页面字体大小
  @media (min-width: 768px) {
    font-size: 18px;
    .key-word {
      font-size: 20px;
    }
    .large-font {
      font-size: 20px;
    }
  }

  // 各个页面，每个块所占高度，保证合起来大致为100%
  .page-one {
    &>.page-block:nth-child(1) {
      height: 15%;
    }
    &>.page-block:nth-child(2) {
      height: 21%;
    }
    &>.page-block:nth-child(3) {
      height: 20%;
    }
    &>.page-block:nth-child(4) {
      height: 40%;
    }
  }
}

.page-up-enter-active,
.page-up-leave-active,
.page-down-enter-active,
.page-down-leave-active {
  will-change: transform;
  transition: all 500ms;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  perspective: 1000;
}
.page-up-enter {
  opacity: 0;
  transform: translate3d(0, -100%, 0);
}
.page-up-leave-active {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
.page-down-enter {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
.page-down-leave-active {
  opacity: 0;
  transform: translate3d(0, -100%, 0);
}
</style>

